import React from "react";
import NavbarOwner from "./NavbarOwner";
import "./Adding.css";
import { useLocation, useNavigate } from "react-router-dom";
import { useState, useEffect } from "react";
import axios from "axios";
export default function AddingCarData() {
  let location = useLocation();
  let navigate = useNavigate();

  let [carData, setcarData] = useState({
    car_name: location?.state?.car_name || location?.state?.carData.car_name,
    car_image: location?.state?.car_image || location?.state?.carData.car_image,
    car_rent: location?.state?.car_rent || location?.state?.carData.car_rent,
    car_fuel: location?.state?.car_fuel || location?.state?.carData.car_fuel,
    car_seating:
      location?.state?.car_seating || location?.state?.carData.car_seating,
    car_number:
      location?.state?.car_number || location?.state?.carData.car_number,
  });

  let name = document.getElementsByName("car_name")[0];
  let img = document.getElementsByName("car_image")[0];
  let rent = document.getElementsByName("car_rent")[0];
  let fuel = document.getElementsByName("car_fuel")[0];
  let seating = document.getElementsByName("car_seating")[0];
  let number = document.getElementsByName("car_number")[0];

  name.value = location?.state?.car_name || location?.state?.carData.car_name;
  img.value = location?.state?.car_image || location?.state?.carData.car_image;
  rent.value = location?.state?.car_rent || location?.state?.carData.car_rent;
  fuel.value = location?.state?.car_fuel || location?.state?.carData.car_fuel;
  seating.value =
    location?.state?.car_seating || location?.state?.carData.car_seating;
  number.value =
    location?.state?.car_number || location?.state?.carData.car_number;

  const HandleForm = (e) => {
    setcarData((prevState) => ({
      ...prevState,
      [e.target.name]: e.target.value,
    }));
  };

  const PostData = () => {
    axios
      .put(
        `https://busy-ruby-starfish-suit.cyclic.app/modifycar/${location?.state?._id}`,
        {
          carData,
        }
      )
      .then((d) => {
        alert("Successfully Updated");

        navigate("/OwnerDash", {});
      })
      .catch((err) => alert("not Updated due to server errors"));
  };

  return (
    <>
      <NavbarOwner />
      <div className="Add">
        <div className="modify">
          <div class="form-box">
            <form class="form">
              <div class="form-container">
                <input
                  type="text"
                  class="input pl"
                  placeholder="car_name"
                  disabled
                />
                <input
                  type="text"
                  class="input"
                  name="car_name"
                  onChange={HandleForm}
                  placeholder=""
                  disabled
                />

                <input
                  type="text"
                  class="input pl"
                  placeholder="car_rent"
                  disabled
                />
                <input
                  type="text"
                  class="input"
                  name="car_rent"
                  onChange={HandleForm}
                  placeholder=""
                />

                <input
                  type="text"
                  class="input pl"
                  placeholder="car_fuel"
                  disabled
                />
                <input
                  type="text"
                  class="input"
                  name="car_fuel"
                  onChange={HandleForm}
                  placeholder=""
                />

                <input
                  type="text"
                  class="input pl"
                  placeholder="car_seating"
                  disabled
                />
                <input
                  type="text"
                  class="input"
                  name="car_seating"
                  onChange={HandleForm}
                  placeholder=""
                  disabled
                />

                <input
                  type="text"
                  class="input pl"
                  placeholder="car_number"
                  disabled
                />
                <input
                  type="text"
                  class="input"
                  name="car_number"
                  onChange={HandleForm}
                  placeholder=""
                />

                <input
                  type="text"
                  class="input pl"
                  placeholder="car_image"
                  disabled
                />
                <input
                  type="text"
                  class="input"
                  name="car_image"
                  onChange={HandleForm}
                  placeholder=""
                  disabled
                />
              </div>
              <button type="button" onClick={PostData}>
                Update
              </button>
            </form>
          </div>
        </div>
      </div>
    </>
  );
}
