import React from "react";
import NavbarOwner from "./NavbarOwner";
import "./Adding.css";
import { useLocation, useNavigate } from "react-router-dom";
import { useState, useEffect } from "react";
import axios from "axios";
export default function NewCar() {
  let navigate = useNavigate();

  let [carData, setcarData] = useState({
    car_name: "",
    car_image: "",
    car_rent: "",
    car_fuel: "",
    car_seating: "",
    car_number: "",
  });

  const HandleForm = (e) => {
    setcarData((prevState) => ({
      ...prevState,
      [e.target.name]: e.target.value,
    }));
  };

  const PostData = () => {
    axios
      .post(`https://busy-ruby-starfish-suit.cyclic.app/postcardata`, {
        carData,
      })
      .then((d) => {
        alert("Successfully Created");

        navigate("/OwnerDash", {});
      })
      .catch((err) => alert("not Updated due to server errors"));
  };

  return (
    <>
      <NavbarOwner />
      <div className="Add">
        <div className="modify">
          <div class="form-box">
            <form class="form">
              <div class="form-container">
                <input
                  type="text"
                  class="input pl"
                  placeholder="car_name"
                  disabled
                />
                <input
                  type="text"
                  class="input"
                  name="car_name"
                  onChange={HandleForm}
                  placeholder=""
                />

                <input
                  type="text"
                  class="input pl"
                  placeholder="car_rent"
                  disabled
                />
                <input
                  type="text"
                  class="input"
                  name="car_rent"
                  onChange={HandleForm}
                  placeholder=""
                />

                <input
                  type="text"
                  class="input pl"
                  placeholder="car_fuel"
                  disabled
                />
                <input
                  type="text"
                  class="input"
                  name="car_fuel"
                  onChange={HandleForm}
                  placeholder=""
                />

                <input
                  type="text"
                  class="input pl"
                  placeholder="car_seating"
                  disabled
                />
                <input
                  type="text"
                  class="input"
                  name="car_seating"
                  onChange={HandleForm}
                  placeholder=""
                />

                <input
                  type="text"
                  class="input pl"
                  placeholder="car_number"
                  disabled
                />
                <input
                  type="text"
                  class="input"
                  name="car_number"
                  onChange={HandleForm}
                  placeholder=""
                />

                <input
                  type="text"
                  class="input pl"
                  placeholder="car_image (upload image url only)"
                  disabled
                />
                <input
                  type="text"
                  class="input"
                  name="car_image"
                  onChange={HandleForm}
                  placeholder=""
                />
              </div>
              <button type="button" onClick={PostData}>
                Create
              </button>
            </form>
          </div>
        </div>
      </div>
    </>
  );
}
