import React from "react";
import Navbar from "./Navbar";
import "./ModifyStatus.css";
import { useState, useEffect } from "react";
import { useLocation, useNavigate } from "react-router-dom";

import axios from "axios";
export default function ModifyStatus() {
  const location = useLocation();
  const navigate = useNavigate();
  const current = new Date();
  const date = `${current.getDate()}-${
    current.getMonth() + 1
  }-${current.getFullYear()}`;
  let [BookingDetails, setData] = useState({
    car_id:
      location?.state?.car_id || location?.state?.BookingDetails.car_id || "",
    car_name:
      location?.state?.car_name ||
      location?.state?.BookingDetails.car_name ||
      "",
    car_image:
      location?.state?.car_image ||
      location?.state?.BookingDetails.car_image ||
      "",
    car_rent:
      location?.state?.car_rent ||
      location?.state?.BookingDetails.car_rent ||
      "",
    car_fuel:
      location?.state?.car_fuel ||
      location?.state?.BookingDetails.car_fuel ||
      "",
    car_seating:
      location?.state?.car_seating ||
      location?.state?.BookingDetails.car_seating ||
      "",
    car_number:
      location?.state?.car_number ||
      location?.state?.BookingDetails.car_number ||
      "",
    location:
      location?.state?.location ||
      location?.state?.BookingDetails.location ||
      "",
    name: location?.state?.name || location?.state?.BookingDetails.name || "",
    mobil:
      location?.state?.mobil || location?.state?.BookingDetails.mobil || "",
    address:
      location?.state?.address || location?.state?.BookingDetails.address || "",
    Date: date || "",
    StartTime:
      location?.state?.StartTime ||
      location?.state?.BookingDetails.StartTime ||
      "",
    EndTime:
      location?.state?.EndTime ||
      location?.state?.BookingDetails?.EndTime ||
      "",
  });

  const PostData = (e) => {
    e?.preventDefault();

    axios
      .put(
        `https://busy-ruby-starfish-suit.cyclic.app/modifybook/${location?.state?._id}`,
        {
          BookingDetails,
        }
      )
      .then(() => {
        alert("Successfully Updated");
        navigate("/status", {});
      })
      .catch((err) => {
        alert("not Updated due to server errors");
      });
  };

  const HandleForm = (e) => {
    setData((prevState) => ({
      ...prevState,
      [e?.target.name]: e?.target.value,
    }));
  };

  let id = document.getElementsByName("car_id")[0];
  let loc = document.getElementsByName("location")[0];
  let fr = document.getElementsByName("StartTime")[0];
  let to = document.getElementsByName("EndTime")[0];
  id.value = location?.state?.car_id || location?.state?.BookingDetails.car_id;
  loc.value =
    location?.state?.location || location?.state?.BookingDetails.location;
  fr.value =
    location?.state?.StartTime || location?.state?.BookingDetails.StartTime;
  to.value =
    location?.state?.EndTime || location?.state?.BookingDetails.EndTime;

  return (
    <>
      <Navbar />
      <div className="modify">
        <div class="form-box">
          <form class="form">
            <div class="form-container">
              <input
                type="text"
                class="input pl"
                placeholder="Car Id"
                disabled
              />
              <input
                type="text"
                class="input"
                name="car_id"
                onChange={HandleForm}
                placeholder=""
              />
              <input
                type="text"
                class="input pl"
                placeholder="Location"
                disabled
              />
              <input
                type="text"
                class="input"
                name="location"
                onChange={HandleForm}
                placeholder=""
              />
              <input
                type="text"
                class="input pl"
                placeholder="Enter Start Time"
                disabled
              />
              <input
                type="datetime-local"
                class="input"
                onChange={HandleForm}
                name="StartTime"
              />
              <input
                type="text"
                class="input pl"
                placeholder="Enter End Time"
                disabled
              />
              <input
                type="datetime-local"
                class="input"
                onChange={HandleForm}
                name="EndTime"
              />
            </div>
            <button onClick={PostData}>Update</button>
          </form>
        </div>
      </div>
    </>
  );
}
